<template>
    <section>

        <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" SubModuleCode="QueueList" IdentityColumn="id" :HeaderText="$t('QueueList')"
            :ListData="QueueListing" :HeaderData="Headers" :PageSize="pageSize" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp"
            :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')" :SearchFields="leftsearchSchema"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:ActionButtonClick="actionButtonClick"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">

            <template v-slot:slotdata="{ data }">

                <template v-if="data.column.COLUMN_NAME == 'progress_value'">
                    <div id="progressBar-30" class="progress-bar progress-bar-striped bg-success"
                        :style="{ width: data.row.progress_value + '%' }" v-if="data.row.progress_value > 0">
                        <span v-if="data.row.progress_value === 100">{{ data.row.progress_value }}</span>
                        <span v-else>{{ data.row.progress_value }}%</span>
                    </div>
                </template>

                <!-- <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.created_at) }}
                    </span>
                </template> -->
                <!-- <template v-if="data.column.COLUMN_NAME == 'status'">
                    <span>
                        <div class="progress mb-1">
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-success"
                                :style="{ width: 100 + '%' }"
                                v-if="data.row.status == 'Completed' && data.row.progressValue == 0">
                                <span>Completed</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-primary"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Generated' && data.row.progressValue == 0">
                                <span>Created</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-danger"
                                :style="{ width: 100 + '%' }"
                                v-else-if="data.row.status == 'Failed' && data.row.progressValue == 0">
                                <span>Failure</span>
                            </div>
                            <div id="progressBar-30" class="progress-bar progress-bar-striped bg-success"
                                :style="{ width: data.row.progressValue + '%' }" v-if="data.row.progressValue > 0">
                                <span v-if="data.row.progressValue === 100">Completed</span>
                                <span v-else>{{ data.row.progressValue }}%</span>
                            </div>
                        </div>
                    </span>
                </template> -->


            </template>

        </tg-list>

    </section>
</template>
<script>
import DataService from '../../services/DataService';

export default ({
    components: {},
    data() {
        return {
            Headers: [
                {
                    COLUMN_NAME: "batch_id",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "batchid",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "date_Range",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "DateRange",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "record_count",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "TotalRecords",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "processed_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "processedby",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "start_time",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "RequestedAt",
                    DISPLAY_ORDER: 5,
                    settings: {
                        isInSlot: false,
                    }
                },
                {
                    COLUMN_NAME: "progress_value",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Progress",
                    DISPLAY_ORDER: 6,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "status",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 7,
                    settings: {
                        isInSlot: false,
                    }
                },

            ],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            leftSearchFields: [],

            leftsearchSchema: [

                {
                    fieldName: 'Fromdate',
                    fieldType: "date",
                    fieldIcon: "fa fa-calendar",
                    fieldSearchConditionName: ['TBP.date_from'],
                    isSearch: false,
                    datePickerConfig: {

                        useCurrent: false,
                        showClear: true,
                        showClose: true,
                        allowInputToggle: true,
                        widgetPositioning: {
                            horizontal: 'right',
                            vertical: 'bottom'
                        }
                    },
                    value: "",
                    listOptions: []
                },
                {
                    fieldName: 'Todate',
                    fieldType: "date",
                    fieldIcon: "fa fa-calendar",
                    fieldSearchConditionName: ['TBP.date_to'],
                    isSearch: false,
                    datePickerConfig: {

                        useCurrent: false,
                        showClear: true,
                        showClose: true,
                        allowInputToggle: true,
                        widgetPositioning: {
                            horizontal: 'right',
                            vertical: 'bottom'
                        }
                    },
                    value: "",
                    listOptions: []
                }

            ],
            listheaderbuttons: [
                {
                    title: 'BacktoList',
                    iconClass: 'fa fa-angle-double-left',
                    callbackfunction: 'BackToList',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                }
            ],
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            QueueListing: [],
        }
    },
    async created() {
        debugger
        var vm = this;
        await vm.FetchData();
    },
    methods: {

        async FetchData() {
            var vueObj = this;
            vueObj.QueueListing = [];
            var url = `search=${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}`;
            const obj = JSON.stringify(url);
            var requestPayload = { objData: obj };
            await DataService.GetBatchProcessingListing(requestPayload).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        let queueListing = response.data.result.data;

                        queueListing.forEach(item => {
                            if (item.status !== 'Completed' && item.status !== 'Failed' && item.status !== 'Generated') {
                                vueObj.QueueListing.push(item);
                            }
                        });

                        // vueObj.QueueListing
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.QueueListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.QueueListing = [];
                }
            })
        },
        leftCommonSearch(value) {
            debugger;
            this.pageNum = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            // switch (callbackfunction) {
            //     case 'Process':
            //         this.callProcessBatch(item, index);
            //         break;
            //     case 'Detail':
            //         this.recordDetail(item);
            //         break;
            //     case 'Reprocess':
            //         this.reProcess(item);
            //         break;
            // }
        },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            // self.listActions.forEach(actionItem => {
            //     let action = { ...actionItem };
            //     switch (action.title) {
            //         case 'Process':
            //             action.isVisible = true;
            //             action.isDisabled = row.status == 'Completed' ? true : false;
            //             break;

            //     }
            //     rtnActionArray.push(action);
            // });
            // return rtnActionArray;
        },
        pagerMethod(value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        getStatusClass(status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "BackToList":
                    this.BackToList();
                    break;
            }
        },

        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },

        BackToList() {
            var vm = this;
            vm.$router.push({ name: 'BatchProcessing' });

        }


    }
})
</script>
